	module.exports = {"data":{"post":{"html":"<p>Christopher's doing better today. He was up at 6:30 reciting his entire vocabulary in alphabetical order. Constistency has improved as well, if you know what I mean. Al had a couple of <a href=\"http://shanson.editthispage.com/discuss/msgReader$546?mode=day\">good suggestions</a>. Substitute potatoes for toast, and you get the same advice we got here.</p>\n<p>Hmmm, can I now post properly with the <a href=\"http://www.opera.com/linux/\">Opera beta 7</a>? It wanted to supress the carriage returns before.</p>\n<p>Let's see if this will be a separate paragraph.... it works!</p>","fields":{"title":"Imagination's playing round for free","date":"01 Apr 2001","url":"https://papascott.de/2001-04-01-imaginations-playing-round-for-free/"}}},"pathContext":{"slug":"/2001-04-01-imaginations-playing-round-for-free/","prev":{"fields":{"title":"I can't let go of you now","date":"31 Mar 2001","slug":"/2001-03-31-i-cant-let-go-of-you-now/"}},"next":{"fields":{"title":"In my world I take him out for tea","date":"02 Apr 2001","slug":"/2001-04-02-in-my-world-i-take-him-out-for-tea/"}}}}

